import uPopup from '../../basic-component/u-popup/u-popup.vue';
export default {
    name: "bd-kb",
    props: {
        limit: String,
        shipping: String,
        express: String,
        pickup: String
    },
    components: {
        uPopup
    },
    data() {
        return {
            show: false,
            showText: '',
            showTitle: ''
        }
    },
    methods: {
        openPopup(data, title) {
            this.showText = this[data];
            this.showTitle = title;
            this.show = true;
        }
    }
}
